.journalItem {
  list-style-type: none;
  &__content {
    width: calc( 100% - 5rem);
    padding: 0 1rem;
  }
  &.journalItem--image {
    background: transparent;
    .journalItem__content {
      img {
        width: 100%;
        max-width: 100%;
      }
    }
    .journalItem__icon {
      display: none;
    }
  }

  &.journalItem--text {
    .journalItem__icon {
      border: 0;
      outline: 0;
      border-right: 1px solid var(--gray-icon-border);
      background-color: transparent;
      padding: 1rem;
      
      svg {
        color: var(--gray-icon);
      }
    }   
  }
  &__controls {
     button {
      outline: 0;
      border: 0;
      border: 1px solid var(--gray-icon-border);
      background: transparent;
      border-top:0;
      border-right: 0;
      margin:0;
      svg {
        color: var(--gray-icon);
      }
      &:hover {
        background: var(--warning);
        svg {
          color: #fff;;
        }
      }
    }
  }
}
