.nav__menu {
  margin: 1rem auto 1.5rem auto;
  font-family: "Montserrat", sans-serif;
  border-radius: 5px;
  padding: 2rem 1rem;
  color: #f9f9f9;
  font-weight: 300;

  h1 {
    font-weight: 400;
    font-size: 2rem;
    margin-top: 3rem;
    margin-bottom: .5rem;
    line-height: 1.2;
  }

  p {
    font-weight: 3rem;
    font-style: italic;
    color: #bbbbbb;
  }

  &__content {
    .calendar {
      width: 95%;
      max-width: 95%;
      margin-top: 4rem;
      table {
        width: 95%;
        max-width: 95%;
        float: none;
      }
    }
  }
}