.dayView {
  margin: 3rem;
  background: var(--transparent-bg);
  padding: .2rem 2rem 2rem 2rem;
  border-radius: 5px;
  width: 75%;
  margin-top: 90px;
  max-width: 800px;
  margin: 90px auto;
  border: 1px solid var(--transparent-white-border);
  overflow: auto;

  h1 {
    color: #ffffff;
    font-size: 18px;
    font-family: var(--ff-heading);
    font-weight: 400;
    letter-spacing: .02rem;
    text-shadow: 0px;
    margin-top: 2rem;
    text-transform: uppercase;
  }

  ul {
    margin: 0;
    padding: 0;

    li {
      position: relative;
      margin: .5rem 0;
      padding: 0;
      list-style-type: none;
      display: flex;
      flex-direction: row;
      flex-wrap: none;
      padding: 0;
      background: var(--transparent-white);
      border-radius: 3px;
      overflow: auto;
    }  
  }

  &__addFormItem {
    display: block !important;
    button {
      width: 100%;
      padding: 1rem .5rem;
      border: 0;
      border-radius: 3px;
      opacity: .7;
    }  
    form {
      padding: 0 1rem;
    }
  }
}  