.nav {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
  overflow: hidden;
  transition: max-height 0.5s ease-in;
   
  &__closed {
    height: 70px;
    max-height: 70px;
    background: transparent;
    overflow: hidden;
  }

  &__open {
    height: auto;
    max-height: 3000px;
    background: #1d1a1ae0;
  }

  &__buttons {
    &__time {
      position: absolute;
      top: 0;
      left: 40px;
      margin-top: 16px;
      font-size: 25px;
      color: #fff;
      font-weight: 300;
      font-family: "Montserrat", sans-serif;
      @media( max-width: 640px ) {
        left: 20px;
      } 
    }
    &__backBtn {
      border: 0;
      color: #fff;
      text-transform: lowercase;
      font-size: 16px;
      font-weight: 300;
      font-family: "Montserrat", sans-serif;

      &:before {
        content: "  \2039" 
      }
    }
    &__menuToggleBtn {
      float: right;
      border: 0;
      margin-top: 10px;
      background: transparent;
  
      span {
        display: block;
        margin-top: 4px;
        background: #fff;
        height: 3px;
        width: 30px;
        transition: all 0.3s ease-in;
      }
    }
  }

  &__open {
    &__buttons {
      &__menuToggleBtn {
        span {
          background: #a9a9a9;
          &:nth-child(1) {
            transform: rotate(45deg);
          }
          &:nth-child(2) {
            transform: rotate(-45deg);
            margin-top: -3px;
          }
        }
      }
    }
  }   
}
@import "menu";